    .group-header {
        display: block;
        // height: 56px;
        position: relative;
        background: #fff;
        border-bottom: 1px solid #e4e4e4;

        p {
            display: inline-block;
            color: $mid-gray;
            font-family: $Signika;
            font-weight: $bold;
            font-size: 16px;
            padding: 20px 0 20px 3em;
            transition: 0.3s ease-in-out;
        }

        &.back {
            &::before {
                content: '';
                position: absolute;
                top: 50%;
                left: 0;
                background: url('./images/back-arrow.jpg') no-repeat;
                background-size: 34px 56px;
                width: 34px;
                height: 100%;
                transform: translateY(-50%);
            }
        }

        &:hover {
            p {
                color: $greensock-green;
            }
        }
    }

    .item {
        position: relative;
        display: block;
        width: 100%;
        // height: 46px;
        border-bottom: 1px solid #e4e4e4;
        background: #ffffff;
        text-decoration: none;

        p {
            display: inline-block;
            color: #777;
            font-family: $Signika;
            font-weight: 400;
            padding: 15px 0 15px 30px;
            transition: 0.3s ease-in-out;
        }

        &:after {
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            background: url('./images/item-arrow.png') no-repeat;
            background-size: 45px 44px;
            width: 45px;
            height: 44px; //100%;
            transform: translateY(-50%);
        }

        .b {
            display: inline-block;
            float: right;
            font-family: $OpenSans;
            font-size: 11px;
            color: #777;
            font-weight: $bold;
            // height: 13px;
            min-width: 20px;
            background: #ededed;
            border-radius: 14px;
            padding: 4px 11px 5px;
            margin: 10px 43px 0 0;
            text-align: center;
            border: 1px solid #d5d5d5;
            box-shadow: 1px 1px 0 rgba(255, 255, 255, 1);
            text-shadow: 1px 1px 0 rgba(255, 255, 255, .6);
        }

        &.item-class, &.item-dtClass {
            // height: 56px;
            p {
                color: $mid-gray;
                font-weight: $bold;
            }

            &:before {
                content: '';
                display: inline-block;
                width: 3px;
                height: 100%;
                background: $greensock-green;
                position: absolute;
                top: 0;
                left: 0;
                transition: 0.3s ease-in-out;
            }
        }

        &.item-page {
            p {}

            &:before {
                content: '';
                display: inline-block;
                width: 3px;
                height: 100%;
                background: $greensock-yellow;
                position: absolute;
                top: 0;
                left: 0;
                transition: 0.3s ease-in-out;
            }

            &:after {
                display: none;
            }

            &.active {
                p {
                    color: $greensock-green;
                }
            }
        }

        &.page-noedit {
            &:before {
                background: transparent;
            }
        }

        &.item-info {
            // height: auto;
            p {
                font-weight: bold;
            }

            &:after {
                display: none;
                padding: 0.5em;
            }
        }

        &.item-element {
            // height: 25px;
            border: none;
            background: #636363;
            padding: 3px 0;
            transition: 0.3s ease-in-out;

            p {
                color: #e1e1e1;
                font-family: $Signika;
                font-weight: 300;
                font-size: 15px;
                padding: 4px 0 4px 30px;
                transition: 0.3s ease-in-out;

                span {
                    color: #a4a4a4;
                }
            }

            &:after {
                display: none;
            }

            &:hover {
                background: #3b3b3b;

                p {
                    color: #fff;
                    padding-left: 35px;
                }
            }

            &.active {
                background: $greensock-green-bright !important;

                p {
                    color: #232323;
                    text-shadow: none;

                    span {
                        color: #232323;
                    }
                }
            }
        }

        &.item-header {
            // height: auto;
            p {
                font-size: 1.1em;
                font-weight: bold;
                padding: 0.5em 30px;
            }
        }

        &.item-class,
        &.item-dtClass,
        &.item-folder,
        &.item-page {
            &:hover {
                background-color: #f8f8f8;

                p {
                    padding-left: 35px;
                    color: $greensock-green;
                }

                &:before {
                    width: 8px;
                }
            }
        }
    }

    .group {
        overflow: hidden;

        .group-header {
            // height: 44px;
            width: 300px;
            border-top: 1px solid #bcbcbc;
            position: relative;
            background: #dedede;

