/* checkbox toggle for docs version */
.checkboxToggleContainer {
  font-size: 14px;
}
.checkboxToggleContainer span {
  display: inline-block;
  margin-top: 1px;
  vertical-align: middle;
}

.checkboxToggle {
  display: none;
}

.checkboxToggle + label {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  background-image: linear-gradient(to top, #f0f0f0, #ffffff);
  color: #4f931b;
  font-size: 14px;
  line-height: 30px;
  height: 30px;
  width: 62px;
  border-radius: 500px;
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  border: solid #cfcfcf 1px;
  font-weight: 400;
}

.checkboxToggle + label .bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-image: linear-gradient(to top, #4e9816, #57a818);
}

.checkboxToggle + label > * {
  transition: all 200ms ease;
}

.checkboxToggle:checked + label {
  color: #fff;
}

.checkboxToggle:checked + label .bg {
  opacity: 1;
}

.checkboxToggle + label .left,
.checkboxToggle + label .right {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.checkboxToggle + label .left {
  padding-left: 10px;
}

.checkboxToggle + label .right {
  left: 100%;
  text-align: right;
  padding-right: 10px;
}

.checkboxToggle + label .dot {
  position: absolute;
  top: 4px;
  left: 5px;
  height: 21px;
  width: 21px;
  border-radius: 50%;
  z-index: 2;
  background-color: white;
  box-sizing: border-box;
  border: solid #cfcfcf 1px;
}

.checkboxToggle:checked + label .dot {
  left: 36px;
}

.checkboxToggle:not(:checked) + label .left,
.checkboxToggle:not(:checked)  + label .right {
  transform: translateX(-100%);
}

